import React, {useEffect} from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components'

const NewsDetail = (props) => {

    const NewsDetailBlock = styled.div`
        border-top: 103.2px solid blue;
        padding: 50px 0px;
        .row {
            text-align: center;
            h2 {
                font-size: 25px;
            }
            img {
                width: 60%;
                padding: 50px 0;
            }
            p.des {
                text-align: left;
            }
        }
    `;

    console.log(props)
    const {location, history} = props;
    useEffect(
        ()=>{
            if (location.state === undefined) {
                history.push('/')
            }
        }
        , []
    )

    if (location.state) {
        return (
            <div>
                <Header />
                <NewsDetailBlock>
                    <div className="row">
                        <h2>{location.state.title}</h2>
                        <p>
                            <img src={location.state.urlToImage} alt={location.state.title} />
                        </p>
                        <p className="des">{location.state.description}</p>
                    </div>
                </NewsDetailBlock>
                <Footer />
            </div>
        );
    } else {
        return null;
    }

};

export default NewsDetail;